.navbar {
  width: 24vw;
  background-color: #f2f3f7;
  position: fixed;
  height: 100vh;
  padding: 20px;
  margin: 6vh 2vw 0;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-content: center;
  /* overflow-y: auto; */
}
.navbar-photo {
  margin-bottom: 0px;
}
.navbar-photo img {
  width: 150px;
  height: 150px;
  object-fit: cover;
  border-radius: 50%;
  padding: auto;
  margin-bottom: 0;
}
.navbar-title {
  margin-top: 0;
}
.header__name {
  margin-bottom: 0.5em;
  text-align: center;
  text-transform: capitalize;
  font-family: "Playfair Display", Georgia, serif;
  font-weight: bold;
  font-size: x-large;
}

.header__details {
  color: #2c98f0;
  margin-bottom: 0;
}
.navbar ul {
  list-style-type: none;
  padding: 0;
  margin: 0 4vw 0;
  text-decoration: none;
  font-family: "Quicksand", Arial, sans-serif;
  transform-origin: left;
}

.navbar ul li {
  font-size: small;
  padding: 5px;
}

.navbar-links ul li a {
  text-decoration: none;
  color: black;
}

.navbar-links ul li a:hover,
.navbar-links ul li a:active {
  text-decoration: underline;
  color: #2c98f0;
}

.navbar-links {
  margin-bottom: 0.4rem;
}

.navbar-list li {
  transform-origin: left;
}
.navbar-list :hover {
  transform: scale(1.1);
}

@media (max-width: 768px) {
  .navbar {
    height: 100vh;
    width: 100vw;
    position: relative;
    margin: 8vh 0 0;
  }
}
