:root {
  --primary: #2166aa;
  --secondary:#2c98f0;
}

html {
  scroll-behavior: smooth;
}
.app {
  display: flex;
  flex-direction: column;
  padding: 0px;
  font-family: "Playfair Display", Georgia, serif;
}

.main-container {
  display: flex;
  max-width: 100vw;
  max-height: 100vh;
}

.content-container {
  flex-grow: 1;
  padding: 10px 10px 0px 0;
  margin-left: 29vw;
  margin-top: 0;
}

.component {
  background-color: #fff;
  color: var(--primary);
  margin-top: 0px;
  padding: 6rem 0 0px;
  height: 90vh;
  margin-bottom: 10px;
}

#photo {
  padding-top: 0;
  height: 55vw;
}

h1,
h2,
h3 {
  font-family: "Playfair Display", Georgia, serif;
  text-transform: uppercase;
  letter-spacing: 5px;
  position: relative;
}

h2 {
  font-size: 25px;
  margin-bottom: 1em;
}

.headings {
  text-shadow: -3px 3px 2px rgb(112, 128, 144);
  margin-left: 5px;
}
h3 {
  font-size: 20px;
  margin-bottom: 1rem;
  letter-spacing: 2px;
}
h4 {
  font-size: 18px;
  margin-bottom: 10px;
}
p {
  font-family: "Quicksand", Arial, sans-serif;
  display: block;
  font-size: large;
  /* font-weight: 250; */
  color: rgba(0, 0, 0, 0.7);
  line-height: 1.5;
}

@media (max-width: 768px) {
  .main-container {
    flex-direction: column;
  }

  .content-container {
    margin-left: 0;
    padding: 10px;
    margin-top: 0;
  }

  .component {
    height: auto;
  }
}

/* Mobile devices */
@media (max-width: 480px) {
  .main-container {
    flex-direction: column;
  }

  .content-container {
    margin-left: 0;
    padding: 10px;
    margin-top: 0;
  }

  .component {
    height: auto;
  }
  h2 {
    font-size: 24px;
  }

  h3 {
    font-size: 18px;
  }

  h4 {
    font-size: 16px;
  }
}
