.project-link {
  text-decoration: underline;
  font-size: 20px;
  margin-bottom: 1rem;
  font-family: "Playfair Display", Georgia, serif;
  text-transform: uppercase;
  position: relative;
  color: var(--secondary);;
}

.project {
  padding: 10px;
  margin: 20px;
  border: 1px groove var(--secondary);;
  box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.2);
}
.project:hover {
  transform: scale(1.05);
}
