.accordion {
  max-width: 62vw;
  margin: 0 auto;
}

.accordion-button {
  background: none;
  border: 1px groove ;
  padding: 20px 0;
  color: #01070e;
  font-size: 20px;
  text-align: left;
  transition: color 0.3s;
}

.accordion-button:focus {
  box-shadow: none;
}
.accordion-button:hover {
  color: #0056b3;
}
.accordion-collapse {
  border-top: 1px solid #eee;
}

.accordion-button:not(.collapsed) {
  color: #333;
  box-shadow: inset 0 -1px 0 #eee;
  background: none;
}
.achievements {
  padding-top: 30px;
  position: sticky;
  top: 80vh;
  z-index: 1;
}

@media (max-width: 768px) {
  .accordion {
    width: 80vw;
    margin: auto;
  }
}
